import './Cell.scss';

import { FontAwesomeIcon } from '@fortawesome/react-fontawesome';
import classNames from 'classnames';
import React, { memo, SyntheticEvent } from 'react';
import CellChecker from 'utils/cell-checker';

import Bomb from '../Bomb';

interface Props {
  cell: Cell;
  onClick?: (cell: Cell) => void;
  onMouseDown?: (event: SyntheticEvent<HTMLButtonElement>, cell: Cell) => void;
  onContextMenu?: (cell: Cell) => void;
}

const Cell = ({ cell, onClick, onMouseDown, onContextMenu }: Props) => {
  const {
    isHidden,
    isEmpty,
    isFlagged,
    isMined,
    isWrong,
    isBusted,
  } = new CellChecker(cell);

  const { value } = cell;

  const className = 'Cell';
  const visibleClassName = `${className} ${className}__visible`;

  const handleClick = () => {
    onClick?.(cell);
  };

  const handleContextMenu = () => {
    onContextMenu?.(cell);
  };

  const handleMouseDown = (event: SyntheticEvent<HTMLButtonElement>) => {
    onMouseDown?.(event, cell);
  };

  if (isHidden) {
    return (
      <button
        type="button"
        aria-label="Cell"
        className={className}
        onClick={handleClick}
        onContextMenu={handleContextMenu}
      />
    );
  }

  if (isFlagged) {
    return (
      <button
        type="button"
        className={className}
        onContextMenu={handleContextMenu}
      >
        <FontAwesomeIcon icon={['far', 'flag']} />
      </button>
    );
  }

  if (isEmpty) {
    return (
      <button type="button" aria-label="Cell" className={visibleClassName} />
    );
  }

  if (isBusted) {
    return <Bomb className={classNames(className, `${className}__busted`)} />;
  }

  if (isMined) {
    return <Bomb className={visibleClassName} />;
  }

  if (isWrong) {
    return (
      <Bomb className={classNames(visibleClassName, `${className}__wrong`)} />
    );
  }

  return (
    <button
      type="button"
      className={classNames(visibleClassName, `${className}__${value}`)}
      onMouseDown={handleMouseDown}
    >
      {value}
    </button>
  );
};

export default memo(Cell);
